body {
  padding:20px;
}
button {
  display:inline-block;
  height:32px;
  line-height:32px;
  border:solid 1px #ddd;
  background:#ddd;
  cursor: pointer;
  box-sizing:border-box;
}
.form-wrap {
  display:flex;
  gap:5px;
  align-items:center;
}
.form-wrap button {
  display:inline-block;
  height:32px;
  line-height:32px;
  border:solid 1px #000;
  background:#000;
  color:#fff;
  cursor: pointer;
  box-sizing:border-box;
}
.form-wrap > div {
  flex:1;
  display:flex;
  align-items:center;
}
.form-wrap > div input[type=text] {
  flex:1;
  height:32px;
  line-height:32px;
  border:solid 1px #000;
  outline-style:none;
  box-sizing:border-box;
  padding:0 10px;
}

.todo-list {
  margin-top:20px;
  list-style:none;
  padding:0;
  border-top:solid 1px #000;
}
.todo-list li {
  padding:10px;
  border-bottom:solid 1px #ddd;
  display:flex;
  gap:10px;
}
.todo-list li a {
  text-decoration:none;
  color:#000;
}
.todo-list li a.end {
  color:#aaa;
  text-decoration:line-through;
}
.todo-list li .btn-modify {
  margin-left:auto;
}

.modal-box {
  margin-top:30px;
  border:solid 1px #000;
  background:#f2f2f2;
  padding:10px;
}
.modal-box h2 {
  font-size:18px;
}
.modal-box p {
  font-size:16px;
}
